import router from './router'
import store from './store'
import i18n from './lang' // 
import { Message } from 'element-ui'
import NProgress from 'nprogress' // progress bar
import 'nprogress/nprogress.css' // progress bar style

NProgress.configure({ showSpinner: false }) // NProgress Configuration

function hasPermission (userType, permissionRoles) {
  if (!permissionRoles) return true
  return permissionRoles.indexOf(userType) >= 0
}

function inRouteList (list, to) {
  return (
    list.indexOf(to.path) !== -1 || // 
    list.some(v => v === to.matched[to.matched.length - 1].path) // 
  )
}

const whiteList = [
  '/',
  '/signup',
  '/signup/email',
  '/signin',
  '/profile',
  '/search',
  '/square',
  '/reset',
  '/project/:id'
] // 
const grayList = ['/profile'] // queryuid

// 
router.beforeEach((to, from, next) => {
  NProgress.start() // start progress bar
  // token
  if (store.getters.token) {
    // 
    if (to.path === '/signin') {
      // next({ path: '/feed' })
      // NProgress.done() // feednextfeedafterEach
      next()
    } else {
      // 
      if (!store.getters.hasUserInfo) {
        store
          .dispatch('GET_USER_INFO') // 
          .then(() => next())
          .catch(() => {
            store.dispatch('SIGN_OUT').then(() => {
              Message.error(i18n.t('errmsgs.getUserInfoFailed'))
              next({ path: '/' })
              // NProgress.done()
            })
          })
      } else {
        // 
        // 
        if (hasPermission(store.getters.userType, to.meta.roles)) {
          next()
        } else {
          next({ path: '/404', replace: true })
          NProgress.done() // need hack
        }
      }
    }
  } else {
    // 
    if (inRouteList(grayList, to) && !to.query.uid) {
      // queryuid
      next({
        path: '/signin',
        query: { returnUrl: to.fullPath } // returnUrl
      })
      NProgress.done() // hack
    } else if (inRouteList(whiteList, to)) {
      // 
      next()
    } else {
      next({
        path: '/signin',
        query: { returnUrl: to.fullPath } // returnUrl
      })
      NProgress.done() // signinnextsigninafterEach
    }
  }
})
router.afterEach(() => {
  NProgress.done() // Progress
  window.scrollTo(0, 0) //  routerscroll-behavior
})



