<i18n>
{
  "zh": {
    "onLimit": " {limit} "
  },
  "en": {
    "onLimit": "Upload {limit} pictures at most"
  }
}
</i18n>

<template>
  <div>
    <el-upload
      ref="upload"
      :limit="limit"
      :on-preview="onPreview"
      :on-remove="onRemove"
      :on-exceed="onExceed"
      :http-request="onUpload"
      :file-list="fileList"
      action=""
      multiple
      list-type="picture-card"
      accept="image/jpeg, image/jpg, image/png, image/bmp">
      <i class="el-icon-plus"/>
    </el-upload>
    <el-dialog :visible.sync="preview.visible">
      <img
        :src="preview.src"
        width="100%" >
    </el-dialog>
  </div>
</template>

<script>
/**
 * 
 */
import { upload } from '@/api/upload'
export default {
  props: {
    // uploadtype
    type: {
      type: String,
      required: true
    },
    // 
    limit: {
      type: Number,
      default: 9
    },
    // 
    imageUrls: {
      type: Array,
      default () {
        return []
      }
    }
  },
  data () {
    return {
      // {uid: id, id: id, url: url, source: , uploading: boolean}
      //  
      uploadList: [],
      fileList: [], // uploadList
      preview: {
        visible: false,
        src: ''
      }
    }
  },
  watch: {
    imageUrls () {
      this.resetLists()
    }
  },
  created () {
    this.resetLists()
  },
  methods: {
    // idfalse
    getImageIds () {
      let res = []
      for (let i = 0; i < this.uploadList.length; i++) {
        let v = this.uploadList[i]
        if (v.uploading) {
          return false
        } else {
          res.push(v.id)
        }
      }
      return res
    },
    // urlfalse
    getImageUrls () {
      let res = []
      for (let i = 0; i < this.uploadList.length; i++) {
        let v = this.uploadList[i]
        if (v.uploading) {
          return false
        } else {
          res.push(v.url)
        }
      }
      return res
    },
    // 
    clear () {
      this.uploadList.forEach(v => v.source && v.source.cancel('')) // 
      this.uploadList = []
    },
    onUpload (file) {
      let fileRaw = file.file
      let uid = fileRaw.uid
      let source = this.$axios.CancelToken.source()

      // 
      this.uploadList.push({
        source,
        uid,
        id: null,
        url: '',
        uploading: true
      })

      // percentloading
      file.onProgress({ percent: 50 })
      upload(this.type, fileRaw, { cancelToken: source.token })
        .then(({ data }) => {
          // id
          this.uploadList.some(v => {
            if (v.uid === fileRaw.uid) {
              v.id = data.id
              v.url = data.path
              v.uploading = false
              return true
            }
            return false
          })
          file.onSuccess()
        }).catch(error => {
          // 
          this.uploadList.filter(v => {
            return v.uid !== fileRaw.uid
          })
          console.info(error)
          file.onError()
        })
    },
    // 
    onRemove (file) {
      this.uploadList = this.uploadList.filter(v => {
        if (file.uid === v.uid) {
          if (v.source) v.source.cancel()
          return false
        }
        return true
      })
    },
    onExceed () {
      this.$message.error(this.$t('onLimit', { limit: this.limit }))
    },
    onPreview (image) {
      this.preview.visible = true
      this.preview.src = image.url
    },
    resetLists () {
      let increment = 0
      this.fileList = this.imageUrls.map(v => {
        return {
          name: 'uploaded',
          uid: Date.now() + increment++, // uid
          url: v
        }
      })
      this.uploadList = this.imageUrls.map((v, index) => {
        return {
          uid: this.fileList[index].uid, // uid
          url: v
        }
      })
    }
  }
}
</script>

<style>
</style>



