import axios from 'axios'
import { Message } from 'element-ui'
import store from '@/store'
import router from '@/router'
import i18n from '@/lang' // 

const service = axios.create({
  baseURL: process.env.BASE_API, // apibase_url
  timeout: 20000 // request timeout
})

// request
service.interceptors.request.use(
  config => {
    config.headers['Accept-Language'] = getLocale() // 
    if (store.getters.token) {
      config.headers['Authorization'] = `Bearer ${store.getters.token}` // token
    }
    return config
  },
  error => {
    // Do something with request error
    console.error('', error) // for debug
    Promise.reject(error)
  }
)

// respone
service.interceptors.response.use(
  response => {
    return response
  },
  error => {
    const response = error.response
    if (axios.isCancel(error)) {
      // 
    } else if (!response) {
      // console.error(error)
      let msg
      if (error.message && error.message.indexOf('timeout') !== -1) {
        msg = getErrMsg('timeout')
      } else {
        msg = getErrMsg('networkError')
      }
      Message({
        message: msg,
        type: 'error',
        duration: 3500,
        showClose: true
      })
    } else {
      // console.error('', error.response)
      if (response.status === 401) {
        store.dispatch('SIGN_OUT').then(() => {
          router.push({
            path: '/signin',
            query: { returnUrl: router.history.current.fullPath } // returnUrl
          })
        })
      } else if (response.status === 403) {
        router.replace('/403')
      } else if (response.status === 429) {
        Message({
          message: getErrMsg(429),
          type: 'error',
          duration: 3500,
          showClose: true
        })
      } else if (response.data) {
        let errmsg = ''
        if (response.data.errors) {
          errmsg = Object.values(response.data.errors)[0] // 
        } else if (response.data.message) {
          errmsg = response.data.message
        }
        if (errmsg) {
          Message({
            message: errmsg,
            type: 'error',
            duration: 3500,
            showClose: true
          })
        }
      }
    }
    return Promise.reject(error)
  }
)

function getErrMsg (key) {
  const ERR_MESSAGES = {
    zh: {
      timeout: '',
      networkError: '',
      429: ''
    },
    en: {
      timeout: 'Timeout, please refresh and try again',
      networkError: 'Network error',
      429: 'Too many requests. Please try again after one minute'
    }
  }
  return ERR_MESSAGES[i18n.locale][key]
}

function getLocale () {
  return i18n.locale === 'zh' ? 'zh-CN' : i18n.locale // zh
}

export default service

export const BASE_API = process.env.BASE_API



