import {
  getThreads,
  getMessagesByThreadId,
  postMessageByThreadId
} from '@/api/message'

var REQ_ID = 0

const notification = {
  state: {
    threads: [], // 
    messages: [], // (thread)
    participant: {} // 
  },

  mutations: {
    RESET: state => {
      state.threads = []
      state.messages = []
      state.participant = {}
    },
    SET_THREADS: (state, threads) => {
      state.threads = threads
    },
    SET_MESSAGES: (state, messages) => {
      state.messages = messages
    },
    SET_PARTICIPANT: (state, participant) => {
      state.participant = participant
    },
    // 
    ADD_MESSAGE: (state, message) => {
      state.messages.push(message)
    }
  },

  actions: {
    // 
    getThreads ({ commit, state }, page = 1) {
      // page1
      // eslint-disable-next-line eqeqeq
      if (page == 1) {
        commit('SET_THREADS', [])
      }
      return getThreads(page).then(
        ({
          data: {
            data: threads,
            meta: {
              pagination,
              extra: { unread_count }
            }
          }
        }) => {
          commit('SET_THREADS', state.threads.concat(threads))
          commit('SET_UNREAD_MESSAGE_COUNT', unread_count)
          return pagination
        }
      )
    },
    getMessagesByThreadId ({ commit, state }, { id, page = 1 }) {
      // page1
      // eslint-disable-next-line eqeqeq
      if (page == 1) {
        commit('SET_MESSAGES', [])
        commit('SET_PARTICIPANT', {})
      }
      var reqId = ++REQ_ID // 
      return getMessagesByThreadId(id, page).then(
        ({
          data: {
            data: messages,
            meta: {
              pagination,
              thread: { participant },
              extra: { unread_count }
            }
          }
        }) => {
          if (reqId !== REQ_ID) {
            return Promise.reject(new Error('out of date'))
          }

          // 
          commit('SET_MESSAGES', messages.reverse().concat(state.messages))
          commit('SET_PARTICIPANT', participant)
          commit('SET_UNREAD_MESSAGE_COUNT', unread_count)
          commit(
            'SET_THREADS',
            state.threads.map(t => {
              if (t.id === id) {
                t.unread_count = 0 // 0
              }
              return t
            })
          )
          return pagination
        }
      )
    },
    // 
    postMessageByThreadId ({ commit, state }, { id, body }) {
      return postMessageByThreadId(id, body).then(({ data }) => {
        commit('ADD_MESSAGE', data)
      })
    }
  }
}

export default notification



